<template>
    <div class="zoom-box">
        <div class="zoom-img" ref="zoomBox">
            <div class="small-box" ref="smallBox" @mousemove="setZoomImg($event)">
                <img :src="currentImg">
                <div class="mask" ref="mask"></div>
            </div>
            <div class="big-box">
                <img :src="currentImg" ref="bigImg">
            </div>
        </div>
        <div class="img-list">
            <div 
                :class="{'img-item': true, active: currentImg==url}" 
                v-for="(url, index) in imgs" :key="index" 
                @mouseenter="currentImg = url">
                <el-image fill :src="url"></el-image>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:'zoom-box',
    props:['imgs'],
    data() {
        return {
            currentImg: this.imgs[1],
            lastIndex: 1,
        }
    },
    watch:{
        imgs(val){
            // 当imgs的值发生变化时，更新 currentImg的值
            this.currentImg = val[1];
        }
    },
    methods: {
       setZoomImg(ev){
            var zoomBox = this.$refs.zoomBox;
            var smallBox = this.$refs.smallBox;
            var mask = this.$refs.mask;
            var bigImg = this.$refs.bigImg;

            // bigImg.offsetHeight/bigBox.offsetHeight = smallBox.offsetHeight/mask.offsetHeight
            // 偏移量的倍数 = bigImg.offsetHeight/smallBox.offsetHeight

            var x = ev.pageX-mask.offsetWidth/2-zoomBox.offsetLeft;
            var y = ev.pageY-mask.offsetHeight/2-zoomBox.offsetTop;
            var ratio = bigImg.offsetHeight/smallBox.offsetHeight;

            var minX = 0;
            var minY = 0;
            var maxX = smallBox.clientWidth - mask.offsetWidth;
            var maxY = smallBox.clientHeight - mask.offsetHeight;

            // 边界限制
            x = x<minX ? minX : x;
            y = y<minY ? minY : y;
            x = x>maxX ? maxX : x;
            y = y>maxY ? maxY : y;

            mask.style.top = y + 'px';
            mask.style.left = x + 'px';

            bigImg.style.left = -mask.offsetLeft*ratio+'px';
            bigImg.style.top = -mask.offsetTop*ratio+'px';
        
       },
    },
}
</script>

<style scoped>
.zoom-box{
    flex-shrink: 0;
    width: 450px;
    height: 540px;
    z-index: 10;
}
.zoom-img{
    width: 100%;
    height: 450px;
    position: relative;
}
.small-box{
    width: 450px;
    height: 450px;
    position: relative;
    box-shadow: 0 0 1px #333 ;
}
.small-box img{
    width: 100%;
    height: 100%;
}
.small-box .mask{
    width: 300px;
    height: 300px;
    background-color: black;
    opacity: .3;
    position: absolute;
    top: 0;
    left: 0;
    display: none;
}
.small-box:hover .mask{
    display: block;
    cursor: move;
}
.big-box{
    width: 540px;
    height: 540px;
    box-shadow: 0 0 1px #333 ;
    overflow: hidden;
    position: absolute;
    left: 450px;
    top: 0;
    display: none;
}
.big-box img{
    width: 810px;
    height: 810px;
    position: absolute;
}
.small-box:hover ~ .big-box{
    display: block;
}

.img-list{
    width: 100%;
    height: 90px;
    display: flex;
    overflow: hidden;
}
.img-list .img-item{
    flex-shrink: 0;
    width: 90px;
    height: 90px;   
    border: 3px solid transparent;
    cursor: pointer;
}
.img-list .img-item:nth-child(1){
    display: none;
}
.img-list .img-item.active{
    border-color: #e4393c;
}
</style>