<template>
	<div class="goods-detail" v-if="goods">
		<!-- 面包屑 -->
		<div class="separator-box">
			<el-breadcrumb class="separator" separator-class="el-icon-arrow-right">
				<el-breadcrumb-item to="/">首页</el-breadcrumb-item>
				<el-breadcrumb-item @click.native="typeOneClick(goods.type_one)" :to="'/typelist/' + goods.type_one">{{ goods.type_one }}</el-breadcrumb-item>
				<el-breadcrumb-item>{{ goods.type_two }}</el-breadcrumb-item>
			</el-breadcrumb>
			<div class="address-box">
				<el-image fill class="img" :src="goods.addressLogo"></el-image>
				<router-link :to="'/supplier/' + goods.supplier">{{ goods.addressName }}旗舰店</router-link>
			</div>
		</div>
		<!-- 商品展示 -->
		<div class="detail-box">
			<zoom-box v-if="goods.imgs" :imgs="goods.imgs"></zoom-box>
			<div class="goods-info">
				<h3>{{ goods.title }} {{ goods.salePoint }}</h3>
				<p class="price">会员价：￥{{ goods.priceStr }}</p>
				<p class="supplier">制造商：{{ goods.supplier }}</p>
				<p class="address">发货地：{{ address.province }}{{ address.city }}{{ address.district }}{{ address.street }}</p>
				<p><el-input-number v-model="count" :min="1" :max="100"></el-input-number></p>
				<p><el-button type="success" @click="addCart">加入购物车</el-button></p>
			</div>
		</div>
		<!-- 店长推荐  -->
		<div class="supplier-list" v-if="supplierList.length">
			<div class="recommend">
				<p><strong>店长推荐</strong></p>
				<router-link :to="'/supplier/' + goods.supplier">更多</router-link>
			</div>
			<div class="supplier-box">
				<goods-item v-for="goods in supplierList" :key="goods.id" :goods="goods"></goods-item>
			</div>
		</div>
		<!-- 商品介绍 -->
		<div class="goods-desc">
			<el-tabs v-model="descName" type="card">
				<el-tab-pane class="desc-img" label="商品详情" name="detail">
					<el-image class="img" v-for="img in this.goods.descriptionImage" :key="img" :src="img"></el-image>
				</el-tab-pane>
				<el-tab-pane label="规格与包装" name="description" class="description">
					<div v-for="(desc, index) in this.goods.description" :key="index">
						<strong>{{ desc.title }}</strong
						>:{{ desc.text }}
						<el-divider></el-divider>
					</div>
				</el-tab-pane>
				<el-tab-pane label="商品评价" name="comment" class="comment">
					<div v-for="(comment, index) in goods.comment" :key="index" :comment="comment">
						<div class="comment-item">
							<div class="user-info">
								<div class="avatar">
									<el-avatar size="large" :src="comment.userPic"></el-avatar>
									<p class="username">{{ comment.userName }}</p>
								</div>
								<p class="vip">{{ comment.vip }}级会员</p>
							</div>
							<div class="comment-info">
								<div class="comment">{{ comment.text }}</div>
								<p class="product">{{ comment.product }}</p>
								<p class="time">{{ comment.time }}</p>
							</div>
						</div>
						<el-divider></el-divider>
					</div>
				</el-tab-pane>
			</el-tabs>
		</div>
	</div>
</template>

<script>
import { getDetailApi, getSupplierListApi, locationApi } from '@/api/goods';
import ZoomBox from '@/components/ZoomBox';
import GoodsItem from '@/components/GoodsItem';

export default {
	name: 'goods-detail',
	components: {
		ZoomBox,
		GoodsItem,
	},
	data() {
		return {
			count: 1,
			goods: null,
			supplierList: [],
			address: {},
			supplierName: 'recommond',
			descName: 'detail',
		};
	},
	beforeRouteEnter(to, from, next) {
		next(vm => {
			vm.getGoods(to);
			vm.descName = 'detail';
			vm.count = 1;
		});
	},
	beforeRouteUpdate(to, from, next) {
		this.getGoods(to);
		this.descName = 'detail';
		this.count = 1;
		next();
	},
	methods: {
		// 加入购物车
		async addCart() {
			await this.$store.dispatch('addCart', { goodId: this.goods.Id, count: this.count });
		},
		// 获取商品信息
		async getGoods(to) {
			let res = await getDetailApi({ goodId: to.params.goodsId });
			this.goods = res[0];
			this.goods.address = JSON.parse(this.goods.address);
			this.goods.comment = JSON.parse(this.goods.comment);
			this.goods.descriptionImage = JSON.parse(this.goods.descriptionImage);
			this.goods.description = JSON.parse(this.goods.description);
			this.goods.imgs = JSON.parse(this.goods.imgs);

			this.getSupplierList(this.goods.supplier);
			this.getAddress(this.goods);
		},
		// 店长推荐
		async getSupplierList(supplier) {
			let res = await getSupplierListApi({ supplier });
			this.supplierList = res;
		},
		// 地址逆解析
		async getAddress(goods) {
			let res = await locationApi({ lat: goods.address.latitude, lng: goods.address.longitude });
			this.address = res.result.addressComponent;

			// https://api.map.baidu.com/reverse_geocoding/v3/?ak=5p8YKL8ZpKuKFwvMOzU0QM05glUx0sDB&output=json&coordtype=wgs84ll&location=37.009946635600,118.093584610100
			// let res = await this.$jsonp('http://api.map.baidu.com/reverse_geocoding/v3', {
			// 	ak: '5p8YKL8ZpKuKFwvMOzU0QM05glUx0sDB',
			// 	output: 'json',
			// 	coordtype: 'wgs84ll',
			// 	location: goods.address.latitude + ',' + goods.address.longitude,
			// });
			// this.address = res.result.addressComponent;
		},
		// 面包屑点击
		typeOneClick(type_one) {
			let index = this.$store.state.typeOneList.indexOf(type_one);
			this.$store.commit('SET_TYPE_ONE_INDEX', index);
		},
	},
};
</script>

<style lang="scss" scoped>
// 面包屑
.separator-box {
	& /deep/ .separator {
		width: 600px;
	}
	.address-box {
		width: 600px;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		& /deep/ .img {
			width: 40px;
			height: 40px;
			margin-right: 20px;
		}
	}
}
// 商品展示
.detail-box {
	margin-top: 20px;
	display: flex;
	justify-content: space-between;
	.goods-info {
		width: 700px;
		height: 540px;
		padding-left: 20px;
	}
	.goods-info > p {
		margin: 20px 0;
	}
}

// 店长推荐
.supplier-list {
	margin-top: 20px;
	.recommend {
		height: 50px;
		line-height: 50px;
		display: flex;
		justify-content: space-between;
		background-color: #ddd;
		padding: 0 20px;
	}
	.supplier-box {
		overflow: hidden;
		margin: 0 -10px;
		display: flex;
	}
}
// 商品介绍
.goods-desc {
	margin-top: 20px;
	box-shadow: 0 0 1px #333;
	.desc-img {
		text-align: center;
		& /deep/ .img {
			width: 60%;
		}
	}
	.description {
		padding: 0 20px;
	}
	.comment-item {
		display: flex;
		margin: 20px 0;
		padding: 0 20px;
		.user-info {
			flex-shrink: 0;
			width: 160px;
			display: flex;
			flex-direction: column;
			.avatar {
				display: flex;
				align-items: center;
				margin-bottom: 10px;
				// .username{
				//     margin-left: 20px;
				// }
			}
		}
		.comment-info {
			.product {
				margin: 10px 0;
			}
		}
	}
}
</style>
