import { getApi } from '@/config/http';

/**
 * 获取banner列表
 */
export const getBannerApi =  async () => {
    let res = await getApi('/banner');
    res = res.map(url=>'http://127.0.0.1:3000'+url);
    return Promise.resolve(res);
}

/**
 * 获取一级分类
 */
